@import "src/assets/style/variables";

.add-product{
}

.product-title{


  p{
    font-size: $font-size-16;
    color: $gold;
    text-align: center;
  }
}


.send-products{
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
  button {
    width: 120px;
    height: 40px;
    border: none;
    outline: none;
    color: #fff;
    background: #111;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
    font-size: $font-size-16;

  }

  button:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left: -2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
  }

  button:active {
    color: #000
  }

  button:active:after {
    background: transparent;
  }

  button:hover:before {
    opacity: 1;
  }

  button:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #111;
    left: 0;
    top: 0;
    border-radius: 10px;
  }

  @keyframes glowing {
    0% {
      background-position: 0 0;
    }
    50% {
      background-position: 400% 0;
    }
    100% {
      background-position: 0 0;
    }
  }
}

.products-add-tools{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.select-categories{
text-align: center;
  p{
     font-size: $font-size-16;
     color: $green;
     padding: 10px;
  }
  select{
    width: 100%;
    height: 30px;
    font-size: $font-size-24;
  }
  span{
    font-size: $font-size-16;
    color: red;
  }
}
.product-image{
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  img{
     width: 100px;
    height: 100px;
  }

  input[type = file]{
     display: none;
  }
  label{
     padding: 10px;
    font-size: $font-size-16;
    color: $gold;
    background-color: $white1;
    border-radius: 10px;
    margin-top: 10px;
    cursor: pointer;
  }

  p{
    font-size: $font-size-16;
    color: red;
  }
}
