@import "src/assets/style/variables";


.carousel-block {
  display: flex;
}

.categories-block {

  p {
    font-size: $font-size-20;
    text-align: center;
  }

}

.categories-box {
  display: flex;
  margin-top: 20px;
  flex-wrap: wrap;
  gap: 10px;
  @media screen and (max-width: 856px) {
    justify-content: center;
    align-items: center;
  }

  @media screen and (max-width: 1632px) {
    align-items: center;
    justify-content: space-between;
  }
  @media screen and (max-width: 851px) {
    align-items: center;
    justify-content: center;
  }

  span {
    font-size: $font-size-16;
    color: red;
  }

}

.featured-products {

  p {
    font-size: $font-size-16;
    text-align: center;

  }
}

.featured-products-block {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 30px;
  justify-content: space-between;


  @media screen and (max-width: 879px) {
    justify-content: center;
    align-items: center;
  }


  span {
    font-size: $font-size-24;
    color: red;
    display: block;
  }
}

.discount-box-carousel {
  width: 40%;
  height: 400px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  cursor: pointer;

  @media screen and (max-width: 1875px) {
    width: 37%;
  }
  @media screen and (max-width: 1719px) {
    width: 600px;
  }
  @media screen and (max-width: 1530px) {
    width: 800px;
  }

}


.discount-block {
  display: flex;
  gap: 10px;
  height: 300px;


  @media screen and (max-width: 1339px) {
    flex-direction: column;
    height: 500px;
  }
}


.carousel-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 10px;
  border-radius: 5px;


  @media screen and (max-width: 1530px) {
    justify-content: center;
    align-items: center;
  }

}

.slider-container {
  width: 58%;
  height: 400px;

  @media screen and (max-width: 1875px) {
    width: 53%;
  }
  @media screen and (max-width: 1719px) {
    width: 850px;
  }
  @media screen and (max-width: 1530px) {
    width: 100%;
  }
}

