@import "src/assets/style/variables";


.header {
  background-color: $light-grey;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1;

}

.title {
  display: flex;
  background-color: $light-grey;
  text-decoration: none;

  h1 {
    padding: 5px;
  }

  .title-multi {
    h1 {
      font-size: $font-size-30;
      color: $gold;
    }
  }

  .title-shop {
    background-color: $gold;

    h1 {
      color: $light-grey;
      font-size: $font-size-30;

    }
  }
}

.header-pages {
  background-color: $light-grey;
}

.title-pages {
  display: flex;
  align-items: center;

}

.categories-burger {
  width: 400px;
  padding: 5px;
  background-color: $gold;

  span {
    font-size: $font-size-30;
  }
}

.menu-pages {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.header-menu {
  min-width: 200px;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;

  ul {
    display: flex;
    justify-content: center;
    align-items: center;

    li {
      list-style: none;

      a {
        font-size: $font-size-20;
        cursor: pointer;
        margin: 20px;
        padding: 15px;
        color: gold;
        text-decoration: none;
        border-radius: 20px;

        &.active {
          background-color: $gold;
          color: $light-grey;
        }
      }
    }
  }

  @media screen and (max-width: 796px) {
    display: none;
  }
}

.fixed-menu {
  position: fixed;
  margin-top: 50px;
  left: 0;
  top: 0;
  width: 100%;
  background-color: $light-grey;
  display: flex;
  align-items: center;
  justify-content: center;
  li{
     list-style: none;
    padding: 10px;
    text-align: center;

    a {
      font-size: $font-size-20;
      cursor: pointer;
      color: gold;
      text-decoration: none;
      border-radius: 20px;

      &.active {
        background-color: $gold;
        color: $light-grey;
      }
    }
  }
}


.heart-basket {
  ul {
    display: flex;

    li {
      list-style: none;

      a {
        margin: 20px;
        padding: 15px;
        font-size: $font-size-20;
        color: gold;
        cursor: pointer;
        text-decoration: none;

        &.active {
          background-color: $gold;
          color: $light-grey;
        }
      }
    }
  }
}

.burger-menu {
  display: none;
  @media screen and (max-width: 796px) {
    display: flex;
    justify-content: space-between;
    align-items: center;


  }
}

.burger-menu {
  span {
    font-size: $font-size-20;
    cursor: pointer;
  }
}

.icon-cart {
  span {
    padding: 10px;
    color: red;
  }
}


.button-login {
  position: relative;
}

.button-login a {
  text-decoration: none;
  cursor: pointer;
  color: white;
  font-family: Helvetica, sans-serif;
  font-weight: bold;
  text-align: center;
  background-color: #FFA12B;
  font-size: $font-size-20;
  position: relative;
  padding: 5px 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  text-shadow: 0px 1px 0px #000;
  filter: dropshadow(color=#000, offx=0px, offy=1px);

  -webkit-box-shadow: inset 0 1px 0 #FFE5C4, 0 10px 0 #915100;
  -moz-box-shadow: inset 0 1px 0 #FFE5C4, 0 10px 0 #915100;
  box-shadow: inset 0 1px 0 #FFE5C4, 0 10px 0 #915100;

  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}

.button-login a:active {
  top: 10px;
  background-color: #F78900;
  -webkit-box-shadow: inset 0 1px 0 #FFE5C4, inset 0 -3px 0 #915100;
  -moz-box-shadow: inset 0 1px 0 #FFE5C4, inset 0 -3pxpx 0 #915100;
  box-shadow: inset 0 1px 0 #FFE5C4, inset 0 -3px 0 #915100;
}

